<template>
  <div id="app">
    <div id='beach-info'>Search beaches that report to the EPA by name, county or state. Using the tool, you can find summary information for each beach that reported information to the agency for years 2017 and 2018.</div>
    <div id='beach-source'>Source: EPA’s BEACON Beach Actions (Advisories and Closures) and Beach Profile Reports</div>
    <beach-filter :states='stateList' @stateSelected='filterState' @searchSpills='searchSpills' />
    <beach-list :beaches='filteredBeaches' />
  </div>
</template>

<script>
import BeachList from './components/BeachList'
import BeachFilter from './components/BeachFilter'
import axios from 'axios'

export default {
  name: 'app',
  data () {
    return {
      beaches: [],
      stateSelected: '',
      searchTerm: '',
    }
  },
  components: {
    'beach-list': BeachList,
    'beach-filter': BeachFilter
  },
  mounted () {
      axios
        .get('https://d2j88q6ypqp8tq.cloudfront.net/beaches/beaches_min.json')
        .then( response => {
          this.beaches = response.data
        })
  },
  computed: {
    filteredBeaches: function () {
      var filteredBeaches = this.beaches
      if ( this.stateSelected !== '' && this.stateSelected !== 'all' ) {
        document.getElementById('beach-notes').style.display = 'block'
        filteredBeaches = filteredBeaches.filter( function (beaches) {
          return beaches.s.match(this.stateSelected)
        }.bind(this))  
      } else {
        filteredBeaches = filteredBeaches.filter( function (beaches) {
          document.getElementById('beach-notes').style.display = 'none'
          return beaches.s.toLowerCase().match('all')
        }.bind(this))  
      }
      if (this.searchTerm !== '') {
        filteredBeaches = filteredBeaches.filter(function (beaches) {
          return (
            beaches.bn.toLowerCase().match(this.searchTerm.toLowerCase()) ||
            beaches.bi.toLowerCase().match(this.searchTerm.toLowerCase()) ||
            beaches.s.toLowerCase().match(this.searchTerm.toLowerCase()) ||
            beaches.c.toLowerCase().match(this.searchTerm.toLowerCase()) ||
            beaches.r.toLowerCase().match(this.searchTerm) ||
            beaches.i.toLowerCase().match(this.searchTerm.toLowerCase()) ||
            beaches.so.toLowerCase().match(this.searchTerm.toLowerCase()) ||
            beaches.na.toString().toLowerCase().match(this.searchTerm.toLowerCase()) ||
            beaches.nd.toString().toLowerCase().match(this.searchTerm.toLowerCase()) ||
            beaches.cl.toString().toLowerCase().match(this.searchTerm.toLowerCase()) ||
            beaches.ra.toString().toLowerCase().match(this.searchTerm.toLowerCase()) ||
            beaches.ca.toString().toLowerCase().match(this.searchTerm.toLowerCase()) ||
            beaches.st.toLowerCase().match(this.searchTerm.toLowerCase())
          )
        }.bind(this))
      }
      return filteredBeaches
    },
    stateList: function() {
      var stateList = [...new Set(this.beaches.map(x=> x.s))]
      return stateList.sort()
    }
  },
  methods: {
    filterState: function (state) {
      this.stateSelected = state
    },
    searchSpills: function (terms) {
      this.searchTerm = terms
    },
  }
}
</script>

