  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #5b5e5e;
  margin-top: 60px;
  max-width:900px;
  margin:20px auto;
}
#beach-info { margin: 20px 0 10px 0; }
#beach-source { color: #333333; font-size:.75em; margin: 0 0 20px 0; }
