<template>
    <div id="beach-category-filter">
        <div id="state-filter">
            <label id="beach_category_select_label" for="beach_category_select" >State: </label>
            <select id="beach_category_select" @change="sortBeachCounty()" v-model="stateSelected">
                <option value="all">- Select a state -</option>
                <option v-for="(state, i) in states" :key="i" :value="state">{{ state }}</option>
            </select>
        </div>
        <div id="beach-search">
          <input id="search-beach" placeholder="Search beaches in state" type="text" class="form-control" v-model="searchTerm" />
        </div> <!-- #beach-search -->
    </div> <!-- #beach-category-filter -->
</template>

<script>
export default {
    name: 'BeachFilter',
    props: ['states'],
    data () {
        return {
            stateSelected: 'all',
            searchTerm: []
        }
    },
    methods: {
        sortBeachCounty: function () {
            this.$emit('stateSelected', this.stateSelected)
        }
    },
    watch: {
      searchTerm: function () {
        this.$emit('searchSpills', this.searchTerm)
      }
    }
}
</script>
<style>
#beach-category-filter {
    display:flex;
    flex-direction: row;
}
#beach-search {
    width:50%;
    margin-left:20px;

}
#search-beach {
    width: 75%;
    height:20px;
}
#state-filter {
    display:flex;
}
#state-filter label {
    margin-right:5px;
    font-size: 1.4em;
}
</style>
